export function Ship(){
    const canv = document.querySelector('#stars');
    const ctx = canv.getContext('2d');
    let canvH = canv.height = window.innerHeight;
    let canvW = canv.width = window.innerWidth;
    let rightControl = document.querySelector("#right");
    let leftControl = document.querySelector("#left");

    let x = canvW/2;
    let y = canvH-50;
    let size = 25;
    let right = false;
    let left = false;
    //uzyteczne
    function addListenerMulti(element, eventNames, listener) {
        var events = eventNames.split(' ');
        for (var i=0, iLen=events.length; i<iLen; i++) {
          element.addEventListener(events[i], listener, false);
        }
    }
    //MOVE RIGHT
    rightControl.addEventListener("touchstart", function(event){
        right=true;
    });
    rightControl.addEventListener("touchend", function(event){
        right=false;
    });
    document.addEventListener("keydown", function(event){
        if(event.key==='d'){
            right=true;
        }
    });
    document.addEventListener("keyup", function(event){
        if(event.key==='d'){
            right=false;
        }
    });

    //MOVE LEFT
    leftControl.addEventListener("touchstart", function(event){
        left=true;
    });
    leftControl.addEventListener("touchend", function(event){
        left=false;
    });
    document.addEventListener("keydown", function(event){
        if(event.key==='a'){
            left=true;
        }
    });
    document.addEventListener("keyup", function(event){
        if(event.key==='a'){
            left=false;
        }
    });

    this.draw = function(){
      ctx.beginPath();
      ctx.arc(x,y,size,0,2*Math.PI);
      ctx.fill();
    }
    this.controls = function(){
      if(right){
          x+=2;
      }
      if(left){
        x+=-2;
    }    
      this.draw();
    }
  }



